.verse {
  padding: 5px;
  line-height: 1.4em;
  font-size: 19px;
}

.verseNumber {
  line-height: 17px;
  font-size: 14px;
  display: inline-block;
  position: relative;
  top: -2px;
  font-weight: bold;
  margin-right: 4px;
  color: #8050C7;
}
.navigationItems-enter {
	opacity: 0.01;
}

.navigationItems-enter-active {
	opacity: 1;
	-webkit-transition: all .2s ease-out;
	transition: all .2s ease-out;
}

.navigationItems-exit {
	opacity: 1;
}

.navigationItems-exit-active {
	opacity: .01;
	-webkit-transition: all .2s ease-out;
	transition: all .2s ease-out;
}


.chapterSelection-enter {
  opacity: 0.01;
  -webkit-transform: translateY(-30px);
          transform: translateY(-30px);
}

.chapterSelection-enter-active {
  opacity: 1;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
	-webkit-transition: all .2s ease-out;
	transition: all .2s ease-out;
}

.chapterSelection-exit {
  opacity: 1;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
}

.chapterSelection-exit-active {
  opacity: 0;
  -webkit-transform: translateY(-30px);
          transform: translateY(-30px);
	-webkit-transition: all .2s ease-out;
	transition: all .2s ease-out;
}


.bookSelection-enter {
  opacity: 0.01;
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
}

.bookSelection-enter-active {
  opacity: 1;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
	-webkit-transition: all .2s ease-out;
	transition: all .2s ease-out;
}

.bookSelection-exit {
  opacity: 1;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
}

.bookSelection-exit-active {
  opacity: 0;
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
	-webkit-transition: all .2s ease-out;
	transition: all .2s ease-out;
}
